.table {
  background-color: $white;
  color: $text-strong;
  margin-bottom: 20px;
  width: 100%;

  td,
  th {
    border: 1px solid $border;
    border-width: 0 0 1px;
    padding: 8px 10px;
    vertical-align: top;

    // Modifiers
    &.icon {
      padding: 5px;
      text-align: center;
      white-space: nowrap;
      width: 1%;

      .fa {
        @include fa(21px, 24px);
      }

      &.link {
        padding: 0;

        & > a {
          padding: 5px;
        }
      }
    }

    &.link {
      padding: 0;

      & > a {
        display: block;
        padding: 8px 10px;

        &:hover {
          background-color: $link;
          color: $link-invert;
        }
      }
    }

    &.narrow {
      white-space: nowrap;
      width: 1%;
    }
  }

  th {
    color: $text-strong;
    text-align: left;
  }

  tr {
    &:hover {
      background-color: $background;
      color: $text-strong;
    }
  }

  thead {
    td,
    th {
      border-width: 0 0 2px;
      color: $text-light;
    }
  }

  tbody {
    tr {
      &:last-child {
        td,
        th {
          border-bottom-width: 0;
        }
      }
    }
  }

  tfoot {
    td,
    th {
      border-width: 2px 0 0;
      color: $text-light;
    }
  }

  // Modifiers
  &.bordered {
    td,
    th {
      border-width: 1px;
    }

    tr {
      &:last-child {
        td,
        th {
          border-bottom-width: 1px;
        }
      }
    }
  }

  &.narrow {
    td,
    th {
      padding: 5px 10px;

      // Modifiers
      &.icon {
        padding: 2px;

        &.link {
          padding: 0;

          & > a {
            padding: 2px;
          }
        }
      }

      &.link {
        padding: 0;

        & > a {
          padding: 5px 10px;
        }
      }
    }
  }

  &.striped {
    tbody {
      tr {
        &:hover {
          background-color: darken($background, 2%);
        }

        &:nth-child(2n) {
          background-color: $background;

          &:hover {
            background-color: darken($background, 2%);
          }
        }
      }
    }
  }
}
