.level-item {
  .title,
  .subtitle {
    margin-bottom: 0;
  }

  // Responsiveness
  @include mobile {
    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
}

.level-left,
.level-right {
  .level-item {
    &:not(:last-child) {
      margin-right: 10px;
    }

    // Modifiers
    &.flexible {
      flex: 1;
    }
  }
}

.level-left {
  // Responsiveness
  @include mobile {
    & + .level-right {
      margin-top: 20px;
    }
  }


  @include tablet {
    align-items: center;
    display: flex;
  }
}

.level-right {
  // Responsiveness
  @include tablet {
    align-items: center;
    display: flex;
    justify-content: flex-end;
  }
}

.level {
  @extend .block;

  align-items: center;
  justify-content: space-between;

  code {
    border-radius: $radius;
  }

  img {
    display: inline-block;
    vertical-align: top;
  }

  // Modifiers
  &.mobile {
    display: flex;

    & > .level-item {
      &:not(:last-child) {
        margin-bottom: 0;
      }

      &:not(.narrow) {
        flex: 1;
      }
    }
  }

  // Responsiveness
  @include tablet {
    display: flex;

    & > .level-item {
      &:not(.narrow) {
        flex: 1;
      }
    }
  }
}
